.sm{
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 27px;
    position: fixed;background-color: black;



    &__list{
        display: flex;
        align-items: center;
    }
    &__link{
        padding: 10px;
        font-size: 16px;
        &--icon{
            color: white;

        }

    }
    @include tablet{
        height: 35px;
    
        &__link{
            padding: 30px;
            font-size: 18px;
    
        }
    }
    @include desktop{
        height: 35px;
    
        &__link{
            padding: 30px;
            font-size: 18px;
    
        }
    }
}

